.notfound-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
	background-color: #f8f9fa;
}

.notfound-title {
	font-size: 48px;
	font-weight: bold;
	margin-bottom: 16px;
	color: #333;
}

.notfound-text {
	font-size: 18px;
	color: #333;
}
